.card-container {
    display: inline-block;
    background-color: lightgreen;
    border-radius: 10px;
    padding: 20px;
    margin: 5px;
    height: 300px;
    width: 200px;
    transition:all 0.6s;
    box-shadow: 0 5px 10px gray;
  }
  .card-container:hover {
        transform: scale(1.05);
    }
  
         
     


.card {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
   align-items: center;
   justify-content: space-around;
   font-size: 16px;
}

.card img {
    width: 150px;
    height: 150px;
}